export function loginByAuth(network, params) {
  $post(httpApi[network].login, params).then(([e, res]) => {
    if (e) {
      console.warn(e);
      return;
    }
    const [err, resv] = statusCode200(res);
    if (err) {
      console.warn(err);
      return;
    }

    console.log(" login_type == 1  ๆฐ็จๆท---> ", resv); // login_type === 1 ๆฐ็จๆท ๆฒกๆๅไธบ่็จๆท
    //ๆฐ็จๆท ไธๅๅค็ ็ดๆฅ่ทณ่ฝฌๅฐๆณจๅ้กต้ข ่ฟ่กๆณจๅ
    if (resv.login_type == 1) {
      location.href = "login.html";
      localStorage.setItem("regLoginShow", true); //ๆฐ็จๆท ๆณจๅๅผน็ชๅผๅณ
    } else {
      //่็จๆท ็ดๆฅ่ทณ่ฝฌ้กต้ข
      successfn(resv); // ๆง่ก ๆง็ ็ปๅฝๅ่ฝ  ไพ่ต
    }
  });
}
